<template>
  <!-- @指數 無過關@ -->
  <div class="straightBasket">
    <v-container
      fluid
      grid-list-xl
    >
      <div class="Pass">指數 - 過關</div>
      <div>
        <div class="firstRow">
          <v-layout
            row
            wrap>
            <v-flex
              xs12
              md3>
              <v-select
                v-model="selectDateItems.selected"
                :items="selectDateItems.options"
                label="日期:"
                class="selectHide"
              />
            </v-flex>
            <v-flex
              xs12
              md3>
              <v-select
                v-model="selectTimeItems.selected"
                :items="selectTimeItems.options"
                label=""
              />
            </v-flex>

            <div
              v-if="intervalTimer !== 0"
              id="timer"
              class="rowText"
            >
              {{ selectedTimer - intervalTimer }}
            </div>

            <v-flex
              xs12
              md2>
              <v-btn
                small
                class="btn1"
                color="deep-orange darken-1"
                @click="updateTimer()">更新</v-btn>
            </v-flex>

            <div class="rowText">
              拆帳：
            </div>

            <v-flex
              xs12
              md2>
              <v-select
                v-model="selectChangeItems.selected"
                :items="selectChangeItems.options"
                label="100%"
              />
            </v-flex>
          </v-layout>
        </div>

        <div class="secondRow">
          <v-layout
            row
            wrap>
            <v-flex
              xs12
              md3>
              <v-select
                v-model="selectRowsPerPageItems.selected"
                :items="selectRowsPerPageItems.options"
                label="每頁顯示"
              />
            </v-flex>
            <div class="rowText">
              共{{ pagination.totalItems }}筆
            </div>
          </v-layout>
        </div>
      </div>

      <div class="pt-1">
        <v-data-table
          :headers="headers"
          :items="sportsList"
          :pagination.sync="pagination"
          :dark="false"
          :default-sort="true"
          :no-data-text="showNoData"
          class="rwdTable no-rwd-table"
          hide-actions
        >
          <template
            slot="items"
            slot-scope="props">

            <td class="text-xs-center report-bills-white-space">{{ props.item.count_date.split(' ')[0] }}</td>
            <td class="text-xs-center" style="min-width: 80px;">{{ props.item.game_time }}</td>
            <td class="text-xs-left report-bills-white-space">
              <CommonTeamName
                :HomeName="props.item.main_team"
              />
            </td>

            <!-- 大小 -->
            <td class="text-xs-right">
              <CommonSportPassBs
                :ProfferOneBs="4.5"
                :VisitBsCompensate="props.item.visit_bs_compensate"
                :MainBsCompensate="props.item.main_bs_compensate"
              />
              <!-- <v-layout>
                <v-flex sm12>
                  <div>
                    4.5&nbsp;
                    {{ props.item.visit_bs_compensate }}
                  </div>
                  <div>
                    {{ props.item.main_bs_compensate }}
                  </div>
                </v-flex>
              </v-layout> -->
            </td>
            <!-- 大小金額 -->
            <td class="text-xs-right">
              <v-layout>
                <v-flex sm12>
                  <div style="min-width: 50px;">
                    <router-link
                      :to="{ path:'/manage/sport/index/AmountIndex' , query: { type: type=2, Posting:props.item.Posting, bet_team: bet_team=0, gameType: '大小',gameSN:props.item.gameSN } }"
                      target="_blank">
                      {{ props.item.visit_count3 }}
                    </router-link>/
                      {{ props.item.visit_gold3 | amountFormat }}
                  </div>
                  <div>
                    <router-link
                      :to="{ path:'/manage/sport/index/AmountIndex' , query: { type: type=2, Posting:props.item.Posting, bet_team: bet_team=1, gameType: '大小',gameSN:props.item.gameSN } }"
                      target="_blank">
                      {{ props.item.main_count3 }}
                    </router-link>/
                      {{ props.item.main_gold3 | amountFormat }}
                  </div>
                </v-flex>
              </v-layout>
            </td>

            <!-- 單雙 -->
            <td class="text-xs-right">
              <CommonSportPassSd
                :VisitSdCompensate="props.item.visit_sd_compensate"
                :MainSdCompensate="props.item.main_sd_compensate"
              />
              <!-- <v-layout>
                <v-flex sm12>
                  <div>
                    單&nbsp;
                    {{ props.item.visit_sd_compensate }}
                  </div>
                  <div>
                    雙&nbsp;
                    {{ props.item.main_sd_compensate }}
                  </div>
                </v-flex>
              </v-layout> -->
            </td>
            <!-- 單雙金額 -->
            <td class="text-xs-right">
              <v-layout>
                <v-flex sm12>
                  <div style="min-width: 50px;">
                    <router-link
                      :to="{ path:'/manage/sport/index/AmountIndex' , query: { type: type=4, Posting:props.item.Posting, bet_team: bet_team=0, gameType: '單雙', gameSN:props.item.gameSN } }"
                      target="_blank">
                      {{ props.item.visit_count5 }}
                    </router-link>/
                      {{ props.item.visit_gold5 | amountFormat }}
                  </div>
                  <div>
                    <router-link
                      :to="{ path:'/manage/sport/index/AmountIndex' , query: { type: type=4, Posting:props.item.Posting, bet_team: bet_team=1, gameType: '單雙', gameSN:props.item.gameSN } }"
                      target="_blank">
                      {{ props.item.main_count5 }}
                    </router-link>
                      {{ props.item.main_gold5 | amountFormat }}
                  </div>
                </v-flex>
              </v-layout>
            </td>

          </template>
        </v-data-table>
        <div class="text-xs-center pt-2">
          <v-pagination
            v-model="pagination.currentPage"
            :length="pages"
            color="deep-orange darken-1"
            @input="loadNextPage"/>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import $ from 'jquery'
import numeral from 'numeral'

export default {
  name: 'MlbStraightBasket',
  filters: {
    // 金錢數字格式
    amountFormat (value) {
      return numeral(value).format('0,0')
      Math.round(number)
    }
  },
  data () {
    return {
      // 日期選擇
      selectDateItems: {
        selected: 1,
        options: []
      },
      // 時間秒數的更新選擇
      selectTimeItems: {
        selected: 60,
        options: [
          { text: '不更新', value: 0 },
          { text: '每5秒更新', value: 5 },
          { text: '每10秒更新', value: 10 },
          { text: '每20秒更新', value: 20 },
          { text: '每30秒更新', value: 30 },
          { text: '每60秒更新', value: 60 }
        ]
      },

      // 拆帳選擇
      selectChangeItems: {
        selected: 1,
        options: [
          { text: '100%', value: 1 },
          { text: '自己', value: 0 }
        ]
      },

      // 每頁顯示筆數
      selectRowsPerPageItems: {
        selected: 15,
        options: [
          { text: '15筆', value: 15 },
          { text: '30筆', value: 30 },
          { text: '60筆', value: 60 }
        ]
      },
      showNoData: '目前無資料',
      headers: [
        { text: '帳務時間', align: 'center', sortable: false },
        { text: '比賽時間', align: 'center', sortable: false },
        { text: '股市名稱', align: 'center', sortable: false },
        { text: '大小', align: 'center', sortable: false },
        { text: '金額', align: 'center', sortable: false },
        { text: '單雙', align: 'center', sortable: false },
        { text: '金額', align: 'center', sortable: false }
      ],
      sportsList: [],
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 15, // 每頁筆數
        totalItems: 0 // 總筆數
      },
      newDate: new Date(),
      // 頁面載入狀態
      loadingStatus: false,
      // 單式球類傳入參數
      gamelist: {
        act: 'allup',
        ball: 11,
        game_type: 0,
        time_interval: '',
        start: '',
        occupy: '',
        count_date: '',
        page: 1,
        count: ''
      },
      // 倒數秒數預設
      intervalTimer: 0,
      selectedTimer: 60,
      interval: ''
    }
  },
  computed: {
    ...mapGetters({
      getSportsState: 'sports/getSportsState'
      // 載入個人帳號
      // getProfileState: 'account-management/getProfileState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    getSportsState (payload) {
      console.log(payload)
      this.sportsList = payload.result.data_list
      // 若 API 有當前頁數回傳資料，可設定如下
      this.pagination.currentPage = payload.result.current_page
      this.pagination.totalItems = payload.result.total_count
      this.sportsList.contents = payload.result.list
    },
    'pagination.rowsPerPage' (rowsPerPage) {
      // 回第一頁
      this.pagination.currentPage = 1
      this.loadSports()
    },
    'selectRowsPerPageItems.selected' (value) {
      this.pagination.rowsPerPage = value
      this.loadSports()
    },
    'selectItems.selected' (value) {
      this.pagination.rowsPerPage = value
      this.loadSports()
    },
    'selectTimeItems.selected' (value) {
      clearInterval(this.interval)
      this.selectedTimer = value
      if (value === 0) {
        this.intervalTimer = 0
      } else if (value === 0 || value === 5 || value === 10 || value === 20 || value === 30 || value === 60) {
        this.intervalTimer = 0
        this.setIntervalTime()
      }
    },
    'selectChangeItems.selected' (value) {
      this.gamelist.occupy = value
      this.loadSports()
    },
    'selectPageItems.selected' (value) {
      this.pagination.rowsPerPage = value
      this.loadSports()
    },
    'selectDateItems.selected' (value) {
    // console.log(this.newDate)
      var date = this.newDate.getFullYear() + '-' + (this.newDate.getMonth() + 1 < 10 ? '0' : '') + (this.newDate.getMonth() + 1) + '-' + (this.newDate.getDate() < 10 ? '0' : '') + this.newDate.getDate()
      // console.log(value)
      // console.log(date)
      // this.gamelist.start = value === date ? '' : 1
      this.gamelist.count_date = value
      this.loadSports()
    },
    getProfileState (payload) {
      this.gamelist.account = payload.result.l_no
    }
  },
  mounted () {
    this.selectedTimer = this.selectTimeItems.selected
    this.setIntervalTime()
    // 打開可傳入正確日期
    let days = [ '星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六' ]
    let options = []
    let newDate = new Date()
    newDate.setDate(newDate.getDate() + 1)
    for (let i = 0; i < 8; i++) {
      console.log(newDate, i)
      options.push(
        { text: newDate.getMonth() + 1 + '月' + newDate.getDate() + '日' + days[newDate.getDay()],
          value: newDate.getFullYear() + '-' + (newDate.getMonth() + 1 < 10 ? '0' : '') + (newDate.getMonth() + 1) + '-' + (newDate.getDate() < 10 ? '0' : '') + newDate.getDate()
        })
      newDate.setDate(newDate.getDate() - 1)
    }
    this.selectDateItems.selected = options[1].value
    // console.log(options)
    this.selectDateItems.options = options
    this.loadSports()
  },
  destroyed () {
    clearInterval(this.interval)
  },
  methods: {
    ...mapActions({
      getSports: 'api/getSports'
    }),
    // 載入過關
    loadSports (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        act: this.gamelist.act,
        ball: this.gamelist.ball,
        game_type: this.gamelist.game_type,
        time_interval: this.gamelist.time_interval,
        start: this.gamelist.start,
        occupy: this.gamelist.occupy ? this.gamelist.occupy : this.selectChangeItems.selected,
        count_date: this.selectDateItems.selected,
        // count_date: this.gamelist.count_date,
        page: page !== undefined ? page : this.pagination.currentPage,
        count: this.pagination.rowsPerPage = this.gamelist.count ? this.gamelist.count : this.selectRowsPerPageItems.selected
      }
      this.getSports(params)
        .then(res => {
          if (res.status === 200) {
            // 載入狀態(結束)
            this.loadingStatus = false
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      this.loadSports()
    },
    setIntervalTime () {
      var self = this
      this.interval = setInterval(function () {
        self.intervalTimer++
        var diffTime = (self.intervalTimer % (self.selectedTimer + 1))
        if (diffTime === 0) {
          self.loadSports()
        }
        $('.straightBasket .firstRow #timer').text(self.selectedTimer - diffTime)
        console.log(diffTime)
      }, 1000)
    },
    updateTimer () {
      this.loadSports()
      if (this.selectedTimer <= 0) {
        clearInterval(this.interval)
      } else {
        clearInterval(this.interval)
        this.selectedTimer = this.selectTimeItems.selected
        this.intervalTimer = 0
        this.setIntervalTime()
      }
    }
  }
}
</script>
